import Vue from "vue";
import Router from "vue-router";
import store from "@/store";

// 增强原方法，用于做页面切换动画
Router.prototype.go = function() {
  this.isBack = true;
  window.history.go(-1);
};

Vue.use(Router);

const routes = [
  {
    path: "/",
    name: "index",
    component: () => import("@/views/index"),
    meta: {
      title: "",
      footer: true
    }
  },
  {
    path: "/rank",
    name: "rank",
    component: () => import("@/views/rank"),
    meta: {
      title: "",
      footer: true
    }
  },
  {
    path: "/user",
    name: "user",
    component: () => import("@/views/user"),
    meta: {
      title: "",
      footer: true
    }
  },
  {
    path: "/test",
    name: "test",
    component: () => import("@/views/test"),
    meta: {
      title: "测试",
      header: true
    }
  },
  {
    path: "/test2",
    name: "test2",
    component: () => import("@/views/test2"),
    meta: {
      title: "测试2",
      header: true
    }
  }
];

const router = new Router({ routes });

router.beforeEach((to, from, next) => {
  try {
    store.commit("updatePageChangeTransition", "");
    if (router.isBack) {
      store.commit("updatePageChangeTransition", "slide-out");
    } else if (
      to.name !== "index" &&
      to.name !== "rank" &&
      to.name !== "user"
    ) {
      store.commit("updatePageChangeTransition", "slide-in");
    }
    router.isBack = false;
    store.commit("updateTitle", to.meta.title);
    store.commit("updateFooter", to.meta.footer);
    store.commit("updateHeader", to.meta.header);
  } catch (error) {
    console.log(error);
  }
  next();
});

export default router;
